<template>
	<div id="wrap">
		<header>
			<div class="C-div">
				<img  @click="back" src="static/images/z.jpg" alt="图片开小差啦">
				<span>爱购店小二</span>
			</div>
		</header>
		<section>
			<div class="firstcart">
				<p class="time">{{timedd}}</p>
				<div class="box">
					<img src="static/images/k.jpg" alt="图片开小差啦">
					<span></span>
					<p>
						您好,店小二爱购很高兴为您服务,请问有什么可以帮助您的吗？请您输入关键字,店小二马上为您排忧解难哦~1
					</p>
				</div>
			</div>
			<div v-for="item1 in data">
				<div class="cart">
					<p class="time">{{item1.timed}}</p>
					<div class="box">
						<img :src="number.img" alt="图片开小差啦">
						<span></span>
						<p>
							{{item1.cont}}
						</p>
					</div>
				</div>
				<div class="cart">
					<p class="time">{{item1.timed}}</p>
					<div class="box">
						<img src="static/images/k.jpg" alt="图片开小差啦">
						<span></span>
						<p>
							{{item1.robot}}
						</p>
					</div>
				</div>
			</div>
		</section>
		<footer>
			<div class="C-fixed">
				<div class="box">
					<img id="img-left" src="static/images/re.jpg" alt="图片被台风吹走啦" class="ren">
					<p>人工服务</p>
				</div>
				<input type="text" @keyup.enter="content" v-model="inptext">
				<img id="img" src="static/images/y.jpg" alt="图片被台风吹走啦" class="ren1">
				<img id="img-right" src="static/images/j.jpg" alt="图片被台风吹走啦" class="ren1" v-show="!inptext.length">
				<button  v-show="inptext.length" @click="content">发送</button>
			</div>
		</footer>
	</div>
</template>
<script>
export default {
	data(){
		return {
			number:{},
			timedd:'',
			inptext:'',
			data:[],
			robot:['现在小店都有优惠活动哦~~ 不错嘀，有时间可以了解一下哦。',
				'蒸包子蒸馒头不争名利，弹吉它弹棉花不谈感情',
				'亲，您看中的这款宝贝是有现货的呢，您可以放心拍哦O(_)O~',
				'工作，退一步海阔天空；爱情，退一步人去楼空！',
				'亲，非常抱歉，我们的定价已经是最低销售价格了呢，没有办法再优惠啦~~~',
				'树不要皮，必死无疑；人不要脸，天下无敌！',
				'爱与恨都是寂寞的空气，哭于笑表达同样的意义',
				'亲~ 客人比较多哦，不要着急呢，我会一个个回复哦，我已经变成三头六臂了',
				'亲~ 价格已调整好，请进入我的淘宝，已买到的宝贝进行支付，支付完请跟我说一下，我要再次跟你核对资料，谢谢',
				'世间纷繁万般无奈，心头只求片刻安宁',
				'我们的质量与手工很好的，是实拍图，请放心购买！',
				'做男人无能会使女人寄希望于未来，做女人失败会使男人寄思念于过去',
				],
		}
	},
	methods:{
		content(){
			var nt=new Date();
			var timed=this.bi((nt.getMonth()+1)) + "-" + this.bi(nt.getDate()) + "  " + this.bi(nt.getHours()) + ":" + this.bi(nt.getMinutes())+ ":" + this.bi(nt.getSeconds());
			if (this.inptext) {
				this.data.push({
					timed:timed,
					cont:this.inptext,
					robot:this.robot[Math.floor(Math.random()*11)],
				})
			}
			this.inptext='';
		},
		back(){
			window.history.go(-1);
		},
		bi(num){				//时间显示判断是否为个位数
			if (num<10) {
				return num='0'+num;
			}else{
				return num;
			}
		},
	},
	mounted(){
		var nt=new Date();
		var timedd=this.bi((nt.getMonth()+1)) + "-" + this.bi(nt.getDate()) + "  " + this.bi(nt.getHours()) + ":" + this.bi(nt.getMinutes())+ ":" + this.bi(nt.getSeconds());
		this.timedd=timedd;
		this.number=JSON.parse(localStorage.getItem("key"));
	},
	updated(){				//滚动条自动到底部
		document.body.scrollTop =document.body.scrollHeight;
	}	
}
</script>
<style scoped>
	#wrap{background:#f9f9f9;min-height: 100vh;}
	header{background: #fff;position: fixed;top:0;width:100%;z-index: 999;max-width: 640px;}
	header .C-div{height:.88rem;border-bottom:1px solid #e6e6e6;font-size:.32rem;text-align:center;}
	header .C-div span{line-height:.88rem;padding-right:.3rem}
	header .C-div img{width:.4rem;height:.4rem;float:left;margin:.28rem 0 0 .32rem;} 
	section{padding: .78rem 0 1.1rem;  overflow-y: scroll;}
	.cart,.firstcart{position:relative;font-size: .26rem;margin-top:.4rem;}
	.cart span,.firstcart span{display:inline-block;border:.2rem solid transparent;margin-top: .4rem}
	.cart img,.firstcart img{width:.8rem;height:.8rem;margin-top:.2rem;border-radius:.7rem}
	
	.cart .box,.firstcart .box{padding:0 .3rem;}
	.cart:after{display: block;content:"";clear:both;}
	.cart:nth-of-type(odd) .box img,.cart:nth-of-type(odd) .box span,.cart:nth-of-type(odd) .box p{float:right;}
	.time{text-align: center;}
	.cart .box p,.firstcart .box p{max-width:3rem;display:inline-block;margin-top:.2rem;border-radius: .1rem;padding:.2rem;background: #fff;}
	.cart .box img,.firstcart .box img{float:left;}
	.cart .box span,.firstcart .box span{border-right:.2rem solid white;float:left;}
	.cart:nth-of-type(odd) .box span{border-right:none; border-left:.2rem solid white;margin-right: .2rem}
	footer{position:fixed;bottom:0;height:.8rem;background:#ebebeb;width:100%;font-size:.26rem;padding:.15rem .2rem;max-width: 640px;}
	.C-fixed .box{display: inline-block;text-align: center;vertical-align: middle;}
	.C-fixed .box p{color:#8b8b8b}
	.C-fixed input{width:4.66rem;height:.7rem;outline:none;border:1px solid gray;border-radius:.1rem;font-size:.26rem;}
	.C-fixed .ren{width:.46rem;height:.46rem;}
	.C-fixed .ren1{width:.54rem;height:.54rem;vertical-align: middle;}
	.C-fixed button{height:.54rem;padding:0 .1rem; outline: none;border:none;color:#fff;background:#65c4aa;line-height: .54rem;border-radius: .08rem}
</style>


